}

.assjob-input-container-label {
  font-weight: 700;
  font-size: 16px;
  line-height: 175%;
  color: #000000;
  width: 200px;
}

.assjob-input-container-label-input {
  background: #f8f8f8;
  border: 1px solid #d5d5d5;
