}

.assjob-submit-btn {
  background: #3a57e8;
  box-shadow: 0px 2px 4px rgba(58, 87, 232, 0.3);
  border-radius: 4px;
  padding: 13px 114px;
  text-align: center;
  width: 259px;
  margin: 38px auto 0 auto;
