  color: #ffffff;
}

.editor2 {
  margin-bottom: 50px;
}

/* Assign job end */

.candidateBlueCard{
  background-color: #c4cdf8;
  box-shadow: rgba(58, 87, 232, 0.3);
  border-radius: 10px;
  padding: 2%;
}
.quickaccess{
  text-decoration: none;
  color: #000000;
